          /* our team section css code starts here  */

          .body{
            
            background-color: white;
            font-family: "Century Gothic";
            min-height: 100vh;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            
        }
        
        
        .title{
            text-align: center;
            text-transform: capitalize;
            color: #726a95;
            margin: 10px 0;
            position: relative;
            margin-top: 20px;
        }
        
        .title::after{
            content: "";
            position: absolute;
            width: 20%; height: 2px;
            background-image: linear-gradient(to left, transparent 5%, #726a95);
            bottom: -10px; left: 50%;
            transform: translate(-50%);
        }
        
        .team-row{
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            padding: 40px 0;
          
            /* overflow: hidden; */
        }
        
        .member{
            flex: 1 1 250px;
            margin: 20px;
            text-align: center;
            padding: 20px 10px;
            cursor: pointer;
            max-width: 300px;
            transition: all 0.3s;
            /* margin-top: -80px; */
            /* overflow: hidden; */
        }
        
        .member:hover{
            box-shadow: 0 10px 20px rgba(0,0,0,0.1);
            transform: translateY(-20px);
        }
        
        .member img{
            display: block;
            width: 150px; height: 150px;
            object-fit: cover;
            border:4px solid #726a95;
            border-radius: 50%;
            margin: 0 auto;
        }
        
        .member h2{
            text-transform: uppercase;
            font-size: 24px;
            color: #726a95;
            margin: 15px 0;
        }
        
        .member p{
            font-size: 15px;
            color: #838383;
            line-height: 1.6;
        }

         
